"use client"
import React, { useEffect, useState } from "react";
import ApiContext, { fetchproducts } from "./ApiContext";
import { categoryFetch } from "../CategoryApi/CategoryApiContext";


const ApiContextProvider = ({children}) => {

    const [products, setProducts] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    
    useEffect(()=>{
        const getProduct = async () =>{
            try{
                const fetchedProducts = await fetchproducts();
                setProducts(fetchedProducts)
                setLoading(false)
            } catch(err){
                setError('Failed to fetch products');
                setLoading(false)
            }
        }
        getProduct();
    },[])

    return(
        <ApiContext.Provider value={{products,loading,error}}>
            {children}
        </ApiContext.Provider>
    )

}



const CategoryApiContextProvider = (children) =>{
    const [category , setCategory] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(()=>{
        const getCatgeoryProduct = async () =>{
            try{
                const categoryFetch = await categoryFetch();
                setCategory(categoryFetch);
                setLoading(false)
            }
            catch(err){
                setError('Failed to fetch products');
                setLoading(false);
            }
        }
        getCatgeoryProduct();
    },[])

    return(
        <CategoryApiContext.Provider value={{category,loading,error}}>
           {children} 
        </CategoryApiContext.Provider>
    )

}

export default (CategoryApiContextProvider , ApiContextProvider);
